.btn-danger-modal {
  position: relative;
  background-color: #df4d5a;
  color: #fff;
  margin: 1.3em auto;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  border-radius: 2em;
  width: fit-content;
}
.btn-danger-modal::after {
  content: "\2192";
  margin-left: 0.2em;
  font-size: 1.8em;
  line-height: 1em;
  transform: translateY(-0.2em);
}

.intro-page {
  display: grid;
  justify-content: center;

  // ==================================
  .wait-anime {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-weight: 700;
  }
  .load-wrapp {
    float: left;
  }
  .bar {
    float: left;
    width: 15px;
    height: 6px;
    border-radius: 2px;
    background-color: red;
  }
  .load-10 .bar {
    animation: loadingJ 1.5s cubic-bezier(0.17, 0.37, 0.43, 0.67) infinite;
  }
  @keyframes loadingJ {
    0%,
    100% {
      transform: translate(0, 0);
    }

    50% {
      transform: translate(200px, 0);
      background-color: #f5634a;
      width: 25px;
    }
  }
  @media (max-width: 767px) {
    .wait-anime {
      top: 80%;
      transform: translateY(0%);
    }
  }
  // ================================

  .search-icon {
    position: absolute;
    width: 2.4em;
    left: 0.2em;
    top: 0;
    opacity: 0.8;
  }

  header {
    width: 100vw;
    background: url("../../header/image/head-bg.png") center center / cover
      no-repeat;
    .header-content {
      max-width: 1300px;
      margin: 0 auto;
    }
  }

  h1 {
    font-size: 3.9em;
    padding: 0 1em;
  }
  h2 {
    padding: 0 1em;
    @media screen and (max-width: 600px) {
      display: none;
    }
  }
}
.vin-input-block {
  display: grid;
  grid-template-columns: 2fr 1fr;
  height: 2.4em;
  overflow: hidden;
  border-radius: 2em;
  padding: 0;
  .search-input {
    padding: 0 0 0 2.6em;
    line-height: 2.5em;
    width: calc(100% + 1em);
    border-radius: 2em;
    color: #000;
    border: none;
    // border: 1px solid #eee;
    &:hover {
      outline: none;
      transition: 0.3s;
      box-shadow: inset 0 0 10px 5px #9b9494;
    }
    &:focus {
      outline: none;
    }
  }
  .check-car-btn {
    z-index: 1;
    background-color: #df4d5a;
    color: #fff;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-around;
    input {
      background: none;
      border: none;
      color: #fff;
      font-weight: 400;
    }
    &:hover {
      transition: 0.3s;
      background-color: #b32c38;
    }
	  img {
		  margin-left: 1em;
		  @media screen and (max-width: 600px) {
			  display: none;
		  }
	  }
  }
}
.vin-input-block-wrapper {
  background: none;
  width: 70%;
  margin: 0 auto;
  position: relative;
  small {
    color: #eee;
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translateY(100%);
  }
}

.instruction {
  max-width: 1300px;
  margin: 0 auto;
  position: relative;
  .line {
    position: absolute;
    top: 0;
    transform: translateY(4.8em);
    width: 100%;
    border-bottom: 1px solid #888;
  }

  display: flex;
  justify-content: space-evenly;
  .instruction-item {
    display: grid;
    justify-content: center;
    justify-items: center;
    position: relative;
    * {
      margin: 0.5em 0;
    }
    .dots {
      position: absolute;
      left: 0.8em;
      top: 0.3em;
      display: flex;
      img {
        margin: 0 0.15em;
      }
    }
  }
}

.instruction-mobile {
  display: none;
}
.header-mobile-links {
  display: none;
}

@media (max-width: 767px) {
  .instruction {
    display: none;
  }
  .instruction-mobile {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 1em;
    align-items: center;
    p {
      padding: 0.5em;
      font-weight: 500;
      word-break: break-word;
      border-left: 1px solid #009c69;
      font-size: 0.9em;
    }
  }
  .header-mobile-links {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 2em;
    div {
      background-color: rgba(255, 255, 255, 0.1);
      border-top-left-radius: 2em;
      border-bottom-left-radius: 2em;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 1em;
      padding: 0.5em 1.5em;
      span {
        width: 2em;
        height: 2em;
        background-color: rgba(255, 255, 255, 0.1);
        border-radius: 0.7em;
        display: flex;
        justify-content: center;
        align-items: center;
        a {
          line-height: 0;
          color: #fff;
          font-size: 1.2em;
        }
      }
    }
  }
  .intro-page {
    h1 {
      font-size: 2.1em;
      margin-bottom: 1em;
    }
  }

  .vin-input-block {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1.5em;
    height: auto;
    border-radius: 0;
    .search-input {
      width: 100%;
    }
    .check-car-btn {
      border-radius: 2em;
    }
  }
  .vin-input-block-wrapper {
    small {
      display: none;
    }
  }
}

// order form block

.order-form-section {
  max-width: 1300px;
  margin: 0 auto;

  padding: 5em 0;
  display: grid;
  justify-content: center;
  justify-items: center;
  // box-shadow: 0 0 40px rgba(219, 217, 217, 0.35);

  h3 {
    img {
      width: 1.3em;
    }
  }
  .order-form {
    margin: 1.5em 0;
    padding: 2.5em 1em;
    box-shadow: 0 0 30px 10px rgba(37, 37, 37, 0.14);
    background: linear-gradient(15deg, #f8f9fa, #ebeced);
    border-radius: 1em;
    h4 {
      width: 80%;
    }
    .form-group {
      margin: 0;
      padding: 0;
      .form-control {
        border-radius: 2em;
      }
      .check-car-btn {
        grid-column: span 2;
        background-color: #df4d5a;
        color: #fff;
        display: flex;
        align-items: center;
        align-content: center;
        justify-content: space-around;
        border-radius: 2em;
        img {
          margin-left: 1em;
          @media screen and (max-width: 600px) {
            display: none;
          }
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .order-form-section {
    margin-top: 0 !important;
    h3 {
      display: grid;
      justify-items: center;
      grid-gap: 0.5em;
    }
  }
}

// price block

.price-block {
  max-width: 1300px;
  margin: 0 auto;

  h2 {
    margin: 1.5em 0 1em 0;
  }
  h4 {
    margin: 0 0 2em 0;
  }
  .price-card {
    button {
      grid-column: span 2;
      background-color: #df4d5a;
      color: #fff;
      margin: 1.3em auto;
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: space-around;
      border-radius: 2em;
      img {
        margin-left: 1em;
      }
    }
    .image-container {
      display: grid;
      justify-items: center;
      justify-content: center;
    }
    .card-image {
      margin: 0.8em 0;
    }
    .price {
      color: #df4d5a;
    }
  }
  .price-description {
    height: 20em;
    border-radius: 1.3em;
    box-shadow: 0 0 40px rgb(219, 217, 217);
    background-color: #fff;
    h5 {
      background-color: #86bb9d;
      border-radius: 1em;
      padding: 0.2em;
      color: #fff;
      margin: 0.4em 2em;
    }
  }
}

// consultation-block

.consultation-block {
  max-width: 1300px;
  margin: 0 auto;

  background-color: #fff;
  width: 100%;
  .text-center {
    .btn {
      grid-column: span 2;
      background-color: #df4d5a;
      color: #fff;
      margin: 1.3em auto;
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: space-around;
      border-radius: 2em;
      img {
        margin-left: 1em;
      }
    }
  }
}

@media (max-width: 768px) {
  .consultation-block {
    .img-holder-block {
      order: 2;
      margin-top: 2.5em;
    }
  }
}
